.innerWrap {
  text-align: center;
}

/* ------------- */
.home {
  position: relative;
}

.invisible {
  max-height: 0;
  overflow: hidden;
}

.contentWrap {
  padding: 60px 0 0;
  border-top: 1px solid #e8f6fb;
}

.innerWrap {
  width: 100%;
  margin: 0 auto 120px auto;
  padding-top: 30px;
}

.header {
  max-width: 500px;
  margin: 0 auto 40px auto;
  background: white;
  box-shadow: 2px 2px 1px 0 rgba(0, 0, 0, 0.06);
  padding: 30px;
}

.title {
  font-size: 24px;
  text-align: left;
}

@media all and (min-width: 800px) {
  .title {
    font-szie: 30px;
    text-align: center;
  }
}

.intro {
  line-height: 1.6;
  text-align: left;
}

.meta {
  color: rgba(85, 85, 85, 0.75);
  font-size: 14px;
  text-align: left;
  line-height: 1.4;
}

/* ------------- */
.legend {
  position: relative;
  text-align: left;
}

.positions {
  margin-top: 10px;
}

.position {
  margin: 3px 0;
}

.cube {
  width: 20px;
  height: 20px;
  display: inline-block;
}

.legendText {
  display: inline-block;
  vertical-align: top;
  margin: 0 4px;
}

.cube.aye {
  background: #00FFB0;
}

.cube.nay {
  background: #FF0055;
}

.cube.none {
  background: gray;
}

/* ------------- */
.issueTab {
  display: inline-block;
  cursor: pointer;
  padding: 4px 8px;
  background: #005060;
  margin: 4px;
  color: white;
  opacity: 0.5;
}

.issueTab:hover, .issueTab.active {
  opacity: 1;
}

.issue {
  margin-top: 30px;
  margin-bottom: 100px;
}

.mapWrap {
  width: 100%;
  overflow: scroll;
}

.maps {
  width: calc(180vw + 16px);
}

@media all and (min-width: 800px) {
  .issue {
    width: 100%;
  }
  .maps {
    width: 100%;
  }
}

/**---*/
.issueBlocks {
  position: relative;
}

.fixedTop {
  opacity: 0;
}

.fixedTop.active {
  opacity: 1;
  position: absolute;
  bottom: 28vw;
  left: calc(50vw - 120px);
  background: rgba(250, 250, 250, 0.9);
  padding-bottom: 10px;
  z-index: 999;
  box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.42);
  width: 180px;
  height: 120px;
}

.districtTitle {
  font-size: 18px;
  font-weight: 800;
  vertical-align: top;
  width: 180px;
  color: white;
  padding: 4px 0;
  background: black;
  margin-bottom: 2px;
}

.arrowImg {
  position: absolute;
  width: 30px;
  left: calc(50% - 15px);
  top: 42px;
}

.peopleItem {
  display: inline-block;
  font-size: 14px;
  width: 50%;
  vertical-align: top;
}

.peopleAvatar {
  width: 45px;
  height: 45px;
  margin: 0 auto 4px auto;
  border: 3px solid lightgray;
  border-radius: 50%;
}

.peopleAvatar.noPeople {
  background: lightgray;
}

.peopleAvatar.KMT {
  border-color: #000095;
}

.peopleAvatar.DPP {
  border-color: #009A00;
}

.peopleAvatar.PFP {
  border-color: #FE6407;
}

.peopleAvatar.TSU {
  border-color: #A25B09;
}

.peopleAvatar.NP, .peopleAvatar.NONE {
  border-color: gray;
}

.peopleAvatar.NSU {
  border-color: rgba(194, 15, 81, 0.9);
}

.peopleAvatar.NPP {
  border-color: #FFD300;
}

.peopleAvatar.MKT {
  border-color: #FFD300;
}

/* ------------- */
.issueTitle {
  font-size: 30px;
}

@media all and (min-width: 800px) {
  .issueTitle {
    display: none;
  }
}

.keypointsWrap {
  max-width: 500px;
  margin: 10px auto 40px auto;
  text-align: left;
  line-height: 1.4;
  padding: 0 10px;
}

.keypointsWrap ol {
  padding-left: 24px;
}

@media all and (min-width: 500px) {
  .keypointsWrap {
    padding: 0;
  }
}

.keypointsTitle {
  font-size: 20px;
  text-align: center;
}

.goToIssue, .goToIssue:visited {
  background: #005060;
  color: white;
  padding: 10px;
  margin-top: 30px;
  display: inline-block;
  text-decoration: none;
}

.goToIssue:hover {
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.12);
}

.powerfulTitle {
  width: 100%;
}

@media all and (min-width: 480px) {
  .powerfulTitle {
    width: 80%;
  }
}



